import { v4 as uuid } from "uuid";
import { Duty } from "../../../utils/const/types/Duty";

export const DutyConstructor = (
  dateStart: Date,
  dateEnd: Date,
  note?: string
) => {
  const newDuty: Duty = {
    id: uuid(),
    dateStart: dateStart,
    dateEnd: dateEnd,
    note: note,
  };
  return newDuty;
};
