import { Button } from "@mantine/core";
import { useDistrigardeStore } from "../../store/Store";
import { IconEdit } from "@tabler/icons";
import { Duty } from "../../../utils/const/types/Duty";

export const DeleteDateButton = (props: { duty: Duty }) => {
  const deleteDuty = useDistrigardeStore((state) => state.deleteDuty);

  const supprimer = (dutyId: string) => {
    deleteDuty(dutyId);
  };
  return (
    <Button
      radius="xl"
      size="xs"
      compact
      variant="filled"
      onClick={() => supprimer(props.duty.id)}
    >
      <IconEdit size={16} /> Supprimer
    </Button>
  );
};
