import { ActionIcon, Badge, Text } from "@mantine/core";
import { IconX } from "@tabler/icons";
import dayjs from "dayjs";
import { useDistrigardeStore } from "../../store/Store";

export const DateBadge = (props: { date: Date; practiceId: string }) => {
  const { date, practiceId } = props;
  const removeButton = <RemoveDateButton date={date} practiceId={practiceId} />;
  return (
    <Badge
      variant="outline"
      sx={{ paddingRight: 3 }}
      rightSection={removeButton}
    >
      <Text>{dayjs(date).format("LL")}</Text>
    </Badge>
  );
};

export const RemoveDateButton = (props: { date: Date; practiceId: string }) => {
  const { date, practiceId } = props;
  const removeNoDutyDateToPractice = useDistrigardeStore(
    (state) => state.removeNoDutyDateToPractice
  );

  const handleRemoveDateToPractice = () => {
    removeNoDutyDateToPractice(practiceId, date);
  };

  return (
    <ActionIcon
      size="xs"
      color="blue"
      radius="xl"
      variant="transparent"
      onClick={handleRemoveDateToPractice}
    >
      <IconX size={10} />
    </ActionIcon>
  );
};
