import dayjs from "dayjs";
import { Duty } from "../../../utils/const/types/Duty";

export const computeDutyType = (d: Duty) => {
  const dutyLength = dayjs(d.dateEnd).diff(dayjs(d.dateStart), "days");
  if (dutyLength === 0) {
    return "shortDuty";
  } else if (dutyLength === 1) {
    return "normalDuty";
  } else {
    return "unknown";
  }
};
