import { generateHolidayDuties } from "./generateHolidayDuties";
import { generateWeekendDuties } from "./generateWeekendDuties";
import { HOLIDAY_FRIBOURG } from "../../../utils/const/HOLIDAY_FRIBOURG";
import { Duty } from "../../../utils/const/types/Duty";
import { sortDutiesByStartDate } from "./sortDutiesByStartDate";
import { mergeNeighboringDuties } from "./mergeNeighboringDuties";

export function assembleNewDuties(values: {
  year: number;
  isAddingWeekends: boolean;
  isAddingHolidays: boolean;
}) {
  let newDuties: Duty[] = [];
  if (values.isAddingHolidays) {
    const holidayDuties: Duty[] = generateHolidayDuties(
      HOLIDAY_FRIBOURG,
      values.year
    );
    newDuties = newDuties.concat(holidayDuties);
  }

  if (values.isAddingWeekends) {
    const weekendDuties: Duty[] = generateWeekendDuties(values.year);
    console.log(JSON.stringify(weekendDuties));
    newDuties = newDuties.concat(weekendDuties);
  }

  newDuties.sort(sortDutiesByStartDate);
  newDuties = mergeNeighboringDuties(newDuties);
  return newDuties;
}
