import { ScrollArea, Table } from "@mantine/core";
import { useDistrigardeStore } from "../../store/Store";
import { DutyRow } from "./DutyRow";

export const DutiesTable = ({}) => {
  const duties = useDistrigardeStore((state) => state.duties);
  const rows = duties.map((d) => <DutyRow duty={d} />);
  return (
    <ScrollArea.Autosize maxHeight={800}>
      <Table sx={{ width: 600 }}>
        <thead style={{ fontWeight: "bold" }}>
          <tr>
            <td>Date</td>
            <td>Notes</td>
            <td>Assigné à</td>
          </tr>
        </thead>
        <tbody>{rows}</tbody>
      </Table>
    </ScrollArea.Autosize>
  );
};
