import { Box, Container } from "@mantine/core";
import { useEffect } from "react";
import { useDrag } from "react-dnd";
import { Practice } from "../../../utils/const/types/Practice";
import { getEmptyImage } from "react-dnd-html5-backend";

export const DraggableBoxShortDuty = (props: { practice: Practice }) => {
  const { practice } = props;
  const [{ isDragging }, dragRef, preview] = useDrag({
    type: "practiceToRow",
    item: {
      dutyType: "shortDuty",
      practice: practice,
      id: practice.id,
      icon: "📱",
    },
    canDrag: () => {
      return practice.nbShortDutiesToDo > 0;
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
      canDrag: monitor.canDrag(),
    }),
  });
  useEffect(() => {
    preview(getEmptyImage());
  }, []);
  let emoj = [];
  for (let i = 0; i < props.practice.nbShortDutiesToDo; i++) {
    emoj.push("📱");
  }

  const emojDisplay = emoj.join(" ");

  return (
    <>
      <Container
        sx={{
          padding: "1em",
          width: "12em",
          height: "4em",
          border: "1px solid black",
        }}
        ref={dragRef}
      >
        <Box>
          Petite garde
          {/* (<span ref={preview}>📱</span>) */}:
          {props.practice.nbShortDutiesToDo}
        </Box>

        <Box>{emojDisplay}</Box>
      </Container>
    </>
  );
};
