import { Box, Container } from "@mantine/core";
import { useEffect } from "react";
import { useDrag } from "react-dnd";
import { Practice } from "../../../utils/const/types/Practice";
import { getEmptyImage } from "react-dnd-html5-backend";
import { CustomDragLayer } from "./CustomDragLayer";

export const DraggableBoxDuty = (props: { practice: Practice }) => {
  const { practice } = props;
  const [{ canDrag, isDragging }, dragRef, preview] = useDrag({
    type: "practiceToRow",
    item: {
      dutyType: "normalDuty",
      practice: practice,
      id: practice.id,
      icon: "☎️",
    },
    canDrag: () => {
      return practice.nbNormalDutiesToDo > 0;
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
      canDrag: monitor.canDrag(),
    }),
  });
  let emoj = [];
  for (let i = 0; i < props.practice.nbNormalDutiesToDo; i++) {
    emoj.push("☎️");
  }
  useEffect(() => {
    preview(getEmptyImage());
  }, []);

  const emojDisplay = emoj.join(" ");

  return (
    <>
      <Container
        sx={{
          padding: "1em",
          width: "12em",
          height: "4em",
          border: "1px solid black",
        }}
        ref={dragRef}
      >
        <Box>
          Garde
          {/* (<span ref={preview}>☎️</span>): */}
          {props.practice.nbNormalDutiesToDo}
        </Box>

        <Box>{emojDisplay}</Box>
      </Container>
      <CustomDragLayer />
    </>
  );
};
