import { Checkbox, NumberInput } from "@mantine/core";
import { Practice } from "../../../utils/const/types/Practice";
import { useDistrigardeStore } from "../../store/Store";
import { AddDateBadge } from "./AddDateBadge";
import { UnavailableDates } from "./UnavailableDates";

export const PracticeRow = (props: { practice: Practice }) => {
  // const removePractice = useDistrigardeStore((state) => state.removePractice);
  const { practice } = props;

  const modifyPracticeEmployeePercentage = useDistrigardeStore(
    (state) => state.modifyPracticeEmployeePercentage
  );
  const modifyPracticeIsIgnoredInCount = useDistrigardeStore(
    (state) => state.modifyPracticeIsIgnoredInCount
  );
  return (
    <tr>
      <td>{practice.name}</td>
      <td>
        <NumberInput
          value={practice.employeePercentage}
          onChange={(val: number) =>
            modifyPracticeEmployeePercentage(practice.id, val)
          }
        />
      </td>
      <td width={300}>
        <UnavailableDates
          dates={practice.noDutyDates}
          practiceId={practice.id}
        />
        <AddDateBadge practiceId={practice.id} />
      </td>
      <td>
        <Checkbox
          checked={practice.isIgnoredInCount}
          onChange={(event) =>
            modifyPracticeIsIgnoredInCount(
              practice.id,
              event.currentTarget.checked
            )
          }
        />{" "}
      </td>
      {/* <td>
              <Button onClick={() => removePractice(practice.id)}>
                Supprimer cabinet
              </Button>
            </td> */}
    </tr>
  );
};
