import {
  Button,
  Checkbox,
  Group,
  NumberInput,
  Stack,
  Title,
  Text,
} from "@mantine/core";
import { useDistrigardeStore } from "../../store/Store";
import { useForm } from "@mantine/form";
import { Duty } from "../../../utils/const/types/Duty";
import { todayYear } from "../SetupDuties";
import { assembleNewDuties } from "../utils/assembleNewDuties";

export const AutoAddDutyForm = (props: {}) => {
  const setDuties = useDistrigardeStore((state) => state.setDuties);

  const form = useForm({
    initialValues: {
      year: todayYear,
      isAddingWeekends: true,
      isAddingHolidays: true,
    },
  });

  const commitAutoAdd = (
    values = {
      year: todayYear,
      isAddingWeekends: true,
      isAddingHolidays: true,
    }
  ) => {
    let newDuties: Duty[] = assembleNewDuties(values);
    setDuties(newDuties);
  };

  return (
    <Stack>
      <Title order={5}>Générer des gardes automatiquement</Title>
      <form onSubmit={form.onSubmit(commitAutoAdd)}>
        <Stack>
          <NumberInput
            placeholder="Année"
            label="Année"
            {...form.getInputProps("year")}
          />
          <Stack>
            <Text>Type</Text>
            <Group>
              <Checkbox
                label="Weekends"
                {...form.getInputProps("isAddingHolidays", {
                  type: "checkbox",
                })}
              />

              <Checkbox
                label="Jours feriés"
                {...form.getInputProps("isAddingHolidays", {
                  type: "checkbox",
                })}
              />
            </Group>
          </Stack>
        </Stack>
        <Group position="right" mt="md">
          <Button radius="xl" variant="filled" type="submit">
            Générer
          </Button>
        </Group>
      </form>
    </Stack>
  );
};
